// Site styles


body,
html {
    min-height: 100vh;
}

body {
    width: 100%;
    min-width: 320px;
    padding: 0;
}

body input[type='email'],
body input[type='submit'],
body input[type='text'],
body textarea {
    -webkit-appearance: none;
}

body,
input,
textarea,
select,
button {
    text-rendering: optimizeLegibility;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

a {
    transition: color .2s linear;
    cursor: pointer;
    outline: none;
}


figure {
    margin: 0;
}

::selection {
    background: #b3d4fc;
}

/*------------------------------------*\
    $SITE
\*------------------------------------*/

.site {
    display: flex;
    min-height: 100vh;
    flex-direction: column;
    .site-content {
        flex: 1;
    }
}

.entry-text {
    text-align: center;
}

.mapBox,
#mapBox {
    width: 100%;
    height: 350px;
    margin: 32px 0;
}

[class^='grid-'],
[class*=' grid-'] {
    background-color: #eee;   
    border: 1px solid rgba(86,61,124,.2);
}



